import React, { Component } from 'react';
import {Container,Form, FormGroup, Label, Input, Button,Alert } from 'reactstrap'

class Tichdiem extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ketqua: [],
            mahoadon:''
        }
    }
    onChange = (e) => {
        this.setState({ mahoadon: e.target.value });
    }

    render() {
        return (
            <Container>
                <Form >
                    <FormGroup>
                        <Label for="mahoadon" />
                        <Input type="text" id="mahoadon" name="mahoadon" placeholder="Nhập mã hóa đơn..." onChange={this.onChange}/>
                    </FormGroup>
                    <Button>Submit</Button>
                   
                    
                </Form>
            </Container>
        );
        
    }
}

export default Tichdiem;